* {font-family:'Nanum Gothic';}

html, body {
  margin: 0;
  height: 100%;
  background: rgb(241, 243, 245);
}

a {
  text-decoration: none;
  color: inherit;
}
button {
  outline: none;
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  /* inherit font & color from ancestor */
  color: inherit;
  font: inherit;
  /* Normalize `line-height`. Cannot be changed from `normal` in Firefox 4+. */
  line-height: normal;
  /* Corrects font smoothing for webkit */
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  /* Corrects inability to style clickable `input` types in iOS */
  -webkit-appearance: none;
  text-align: inherit; 
}
button::-moz-focus-inner {
  border: 0;
  padding: 0; 
}
.util.flex-center {
  display: flex;
  align-items: center;
  justify-content: center; }

.util.flex-placeholder {
  flex: 1 1; }
      
.SubmitBox {
  position: absolute;
  right: 0.5rem;
  top: 5rem;
  background: #495057;
  width: 20rem;
  padding: 1.5rem;
  z-index: 10;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  color: white;
}

@-webkit-keyframes submitBoxAppear {
  0% {
      opacity: 0;
      -webkit-transform: translateY(-25%);
      transform: translateY(-25%);
  }

  100% {
      opacity: 1;
      trasnform: translateY(0px);
  }
}

@keyframes submitBoxAppear {
  0% {
      opacity: 0;
      -webkit-transform: translateY(-25%);
      transform: translateY(-25%);
  }

  100% {
      opacity: 1;
      trasnform: translateY(0px);
  }
}

@-webkit-keyframes submitBoxDisappear {
  0% {
      opacity: 1;
      trasnform: translateY(0px);
  }

  100% {
      opacity: 0;
      -webkit-transform: translateY(-25%);
      transform: translateY(-25%);
  }
}

@keyframes submitBoxDisappear {
  0% {
      opacity: 1;
      trasnform: translateY(0px);
  }

  100% {
      opacity: 0;
      -webkit-transform: translateY(-25%);
      transform: translateY(-25%);
  }
}

@media (max-width: 375px) {
  .SubmitBox {
      width: calc(100vw - 1rem);
  }
}

.SubmitBox.appear {
  -webkit-animation: submitBoxAppear 0.15s ease-in-out;
  animation: submitBoxAppear 0.15s ease-in-out;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

.SubmitBox.disappear {
  -webkit-animation: submitBoxDisappear 0.15s ease-in-out;
  animation: submitBoxDisappear 0.15s ease-in-out;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

.SubmitBox .title {
  display: flex;
  align-items: center;
  font-size: 2rem;
}

.SubmitBox .title .text {
  font-size: 1.25rem;
  font-weight: 600;
}

.SubmitBox .title .view {
  margin-left: auto;
  font-size: 0.9rem;
  color: #fd7e14;
  font-weight: 600;
}

.SubmitBox .title .view:hover {
  cursor: pointer;
  color: #ff922b;
}

.SubmitBox .sections {
  margin-top: 0.5rem;
}

.SubmitBox .sections section {
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.SubmitBox .sections .section-title {
  font-size: 0.8rem;
  font-weight: 600;
  margin-bottom: 0.75rem;
  color: #ced4da;
}

.SubmitBox .sections .section-title.category {
  display: flex;
  align-items: center;
}

.SubmitBox .sections .section-title.category .edit {
  line-height: 0.8rem;
  margin-left: auto;
  padding: 0.25rem;
  border: 1px solid #f8f9fa;
  border-radius: 2px;
  color: white;
  cursor: pointer;
}

.SubmitBox .sections .section-title.category .edit div {
  margin-top: 0.1rem;
  margin-left: 0.25rem;
}

.SubmitBox .sections .section-title.category .edit:hover {
  background: white;
  color: #212529;
}

.SubmitBox .footer {
  padding-top: 1rem;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin: -1.5rem;
  padding: 1.5rem;
  padding-bottom: 1rem;
  margin-top: 0rem;
  background: #343a40;
}

.SubmitBox .footer .buttons {
  display: flex;
}

.SubmitBox .footer .buttons button {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1 1;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  border-radius: 4px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.12), 0 3px 6px rgba(0, 0, 0, 0.1725);
  font-weight: 500;
  font-size: 0.9rem;
}

.SubmitBox .footer .buttons button.gray {
  background: #65707b;
}

.SubmitBox .footer .buttons button.gray:hover {
  background: #707d89;
}

.SubmitBox .footer .buttons button.gray:active {
  background: #5a636d;
}

.SubmitBox .footer .buttons button.purple {
  background: #845ef7;
}

.SubmitBox .footer .buttons button.purple:hover {
  background: #9776f8;
}

.SubmitBox .footer .buttons button.purple:active {
  background: #845ef7;
}

.SubmitBox .footer .buttons button.blue {
  background: #339af0;
}

.SubmitBox .footer .buttons button.blue:hover {
  background: #4ba6f2;
}

.SubmitBox .footer .buttons button.blue:active {
  background: #339af0;
}

.SubmitBox .footer .buttons button+button {
  margin-left: 1rem;
}

.SubmitBox .footer .open-options {
  text-align: right;
  font-size: 0.9rem;
  color: #f76707;
  display: block;
  margin-top: 1rem;
  margin-right: 0.125rem;
  font-weight: 600;
}

.SubmitBox .footer .open-options a:hover {
  cursor: pointer;
  color: #fd7e14;
}

.InputTags .input-button {
  display: flex;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.12), 0 3px 6px rgba(0, 0, 0, 0.1725);
}

.InputTags .input-button input {
  font-size: 1rem;
  padding: 0.5rem;
  flex: 7 1;
  border: none;
  outline: none;
}

.InputTags .input-button .button {
  flex: 2 1;
  background: #60686f;
  color: white;
  font-weight: 600;
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
  font-size: 0.9rem;
  transition: 0.05s all ease-in;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
}

.InputTags .input-button .button:hover {
  background: #868e96;
}

.InputTags .input-button .button:active {
  background: #555b61;
}

.InputTags .tags {
  margin-top: 0.5rem;
  display: flex;
  flex-wrap: wrap;
  margin-left: -0.25rem;
  margin-right: -0.25rem;
  max-height: 5.5rem;
  overflow-y: auto;
  position: relative;
}

.InputTags .tags .tag {
  margin: 0.25rem;
  padding: 0.25rem;
  padding-left: 0.5rem;
  padding-right: 0rem;
  font-size: 0.85rem;
  color: #212529;
  align-items: center;
  display: flex;
  background: #e9ecef;
  font-weight: 500;
  word-break: break-all;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.12), 0 3px 6px rgba(0, 0, 0, 0.1725);
  border-radius: 6px;
}

.InputTags .tags .tag .remove {
  font-size: 0.9rem;
  color: #868e96;
  padding-left: 0.25rem;
  padding-right: 0.25rem;
}

.InputTags .tags .tag .remove:hover {
  cursor: pointer;
  color: #c92a2a;
}
/*
 * Container style
 */
 .ps {
  overflow-anchor: none;
  -ms-overflow-style: none;
  touch-action: auto;
  -ms-touch-action: auto;
}

.ps::-webkit-scrollbar-track{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
}

.ps::-webkit-scrollbar{
	width: 6px;
	background-color: #F5F5F5;
}

.ps::-webkit-scrollbar-thumb{
	background-color: #000000;
}
.WriteConfigureThumbnail {
  position: relative;
}

.WriteConfigureThumbnail .remove-wrapper {
  margin-top: -0.75rem;
  text-align: right;
  padding-right: 0.25rem;
  margin-bottom: 0.5rem;
}

.WriteConfigureThumbnail .remove-wrapper .remove {
  font-size: 0.875rem;
  font-weight: 800;
  color: #ff6b6b;
  cursor: pointer;
}

.WriteConfigureThumbnail .remove-wrapper .remove:hover {
  color: #ff8787;
}

.WriteConfigureThumbnail .white-button {
  cursor: pointer;
  border-radius: 4px;
  padding: 0.5rem 0.75rem;
  background: white;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.12), 0 3px 6px rgba(0, 0, 0, 0.1725);
  color: #343a40;
  display: flex;
  align-items: center;
}

.WriteConfigureThumbnail .white-button.fullwidth {
  width: 100%;
  justify-content: center;
}

.WriteConfigureThumbnail .white-button:hover {
  background: #f1f3f5;
}

.WriteConfigureThumbnail .white-button svg {
  font-size: 1.25rem;
}

.WriteConfigureThumbnail .white-button .text {
  margin-left: 0.5rem;
  font-size: 1rem;
  font-weight: 800;
}

.WriteConfigureThumbnail .thumbnail-area {
  width: 100%;
  padding-top: 52.63%;
  position: relative;
}

.WriteConfigureThumbnail .thumbnail-area .full {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.WriteConfigureThumbnail .thumbnail-area img {
  object-fit: cover;
  display: block;
}

.WriteConfigureThumbnail .thumbnail-area .overlay {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  flex-direction: column;
  padding-right: 0.875rem;
  padding-bottom: 0.875rem;
}

